<template>
    <v-container class="pa-0" style="position: relative;">
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon color="primary"
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{accountText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card style="margin-top: 60px;">
            <v-list active-class="primary--text" style="padding: 0px!important">
                <v-list-item @click="$router.push({name: 'settingsAccountEditNameAndLastName'})">
                    <v-list-item-content>
                        <v-list-item-title>{{nameAndLastNameText}}</v-list-item-title>
                        <v-list-item-subtitle>{{name}} {{lastName}}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item @click="$router.push({name: 'settingsAccountPasswordVerification'})">
                    <v-list-item-content>
                        <v-list-item-title>{{emailText}}</v-list-item-title>
                        <v-list-item-subtitle>{{email}}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item @click="$router.push({name: 'settingsAccountEditPassword'})">
                    <v-list-item-content>
                        <v-list-item-title>{{passwordText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-card>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";

    export default {
        name: "SettingsAccount",
        data() {
            return {
                snackbar: false,
                snackbarText: null,
                closeText: Constants.TEXT_CLOSE,
                accountText: Constants.TEXT_ACCOUNT,
                nameAndLastNameText: Constants.TEXT_NAME_AND_LASTNAME,
                name: this.$store.getters['user/getName'],
                lastName: this.$store.getters['user/getLastName'],
                emailText: Constants.TEXT_EMAIL_DIRECTION,
                email: this.$store.getters['user/getEmail'],
                passwordText: Constants.TEXT_PASSWORD,

            }
        },

    }
</script>

<style scoped>

</style>